

.building{
    /* border: 2px solid black; */
    display: flex;
    /* width: 100vw; */
    /* height: 50vh; */
    padding: 10px;
    
}

.lift{
    position: relative;
    /* border: 2px solid red; */
    width: 50%;
    padding: 10px;
    background-color: #2c2c2c;
}

.lift-info{
    border: 2px solid purple;
    background-color: #5c5975;
}

.blocks{
    /* border: 2px solid black; */
    /* box-sizing: border-box; */
    width: 150px;
    height: 100px;
    margin-bottom: 10px;
    display: flex;
    text-align: center;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    background-color: blue;
    color: white;
    border-radius: 10px;
}

.lifter{
    background-color: aqua;
    width: 100px;
    height: 100px;
    position: absolute;
    left: 180px;
    top: 10px;
    border-radius: 10px;
    overflow: auto;
    transition: all 1s;

}
.lifter div{
    background-color: #2c2c2c;
    width: 100px;
    height: 100px;
    position: absolute;
    border-radius: 10px;
    left: -100%;
    transition: left;
    transition-delay: 1s;
}

@keyframes door-animation1{
    0%{
        width: 100px;
    }
    50%{
        width: 0px;
    }
    100%{
        width: 100px;
    }
}

@keyframes door-animation2{
    0%{
        width: 100px;
    }
    50%{
        width: 0px;
    }
    100%{
        width: 100px;
    }
}

@keyframes door-animation3{
    0%{
        width: 100px;
    }
    50%{
        width: 0px;
    }
    100%{
        width: 100px;
    }
}

.lift-1{
    top: 230px;
    /* animation: door-animation1 2s  1s   ; */
}
.lift-2{
    top: 120px;
    /* animation: door-animation2 2s  1s   ; */

}
.lift-3{
    top: 10px;
    /* animation: door-animation3 2s  1s   ; */

}

@keyframes door1{
    0%{
        left : -100%
    }
    25%{
        left: 0%;
    }
    50%{
        left : 0%
    }
    75%{
        left : 0%
    }
    100%{
        left: -100%;
    }
}
@keyframes door2{
    0%{
        left : -100%
    }
    25%{
        left: 0%;
    }
    50%{
        left : 0%
    }
    75%{
        left : 0%
    }
    100%{
        left: -100%;
    }
}
@keyframes door3{
    0%{
        left : -100%
    }
    25%{
        left: 0%;
    }
    50%{
        left : 0%
    }
    75%{
        left : 0%
    }

    100%{
        left: -100%;
    }
}


.lift-1 div{
    animation: door1 3s 1s;
}
.lift-2 div{
    animation: door2 3s 1s;
}
.lift-3 div{
    animation: door3 3s 1s;
}

.btn-container{
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

.btn{
    background-color: grey;
    margin-bottom: 10px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    color: white;
    font-size: 2rem;
    cursor: pointer;
    /* background-color: Transparent; */
}


.person{
    color: white;
    position: absolute;
 
    opacity: 0;
    transform: scale(3);
    top: 50px;
    left: 70px;

    transition: opacity 3s 1s;
    
}

@keyframes perAnim{
    0%{
        opacity: 1;
        left: 70px;
    }
    50%{
        opacity: 1;
    }
    100%{
        left: 230px;
        opacity: 0;
    }
}


.person-anim-1{
    /* display: block; */
    
    top: 270px;
    animation: perAnim 3s  1s forwards;

}
.person-anim-2{
    /* display: block; */
    
    top: 160px;
    animation: perAnim 3s  1s   forwards;

}
.person-anim-3{
    /* display: block; */
    
    top: 50px;
    animation: perAnim 3s  1s   forwards;

}

@keyframes perAnimOut{
    0%{
        opacity: 1;
        left: 230px;
    }
    /* 99%{
        opacity: 1;
    } */
    50%{
        opacity: 1;
    }
    100%{
        left: 70px;
        opacity: 0;
    }
}

.person-out-1{
    
    top: 270px;
    animation: perAnimOut 3s  1s forwards;
}
.person-out-2{
    
    top: 160px;
    animation: perAnimOut 3s  1s forwards;
}
.person-out-3{
   
    top: 50px;
    animation: perAnimOut 3s  1s forwards;
}



@media screen and (max-width: 500px) {
    body {
      background-color: palegreen;
    }

    .building{
        flex-direction: column;
        
    }
    
.lift{
   
    width: 90%;
    border-radius: 10px;
    /* padding: 10px; */
}
    .lift-info{
        width: 96%;
        margin-top: 10px;
        border-radius: 10px;
    }
    .btn-container{
        
        padding: 10px;
        flex-direction: row-reverse;
        justify-content: flex-end;
    }
    .btn{
        margin-right: 5px;
    }
    

  }